.about-container {
  min-height: 38rem;
  position: relative;
  padding: 5rem;
  padding-top: 2rem;

  .container__header {
    padding-left: 4rem;
  }

  &__profile {
    width: 15rem;
    height: 15rem;
    overflow: hidden;
    background-color: $secondaryBgColor;
    position: absolute;
    z-index: 2;
    top: calc(50% - 7.5rem);
    left: -8rem;
    border: 0.25rem solid $supportColor;
    border-radius: 0.5rem;

    img {
      height: 100%;
      object-fit: contain;
      transform: scale(1);
      transition: 0.3s ease-in-out;

      &:hover {
        transform: scale(1.025);
      }
    }
  }

  &__details {
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    width: calc(100% - 8rem);
    position: relative;
    left: 8rem;
    border: 0.25rem solid $supportColor;
    border-radius: 0.5rem;
    padding: 2rem;
    min-height: 25rem;
    gap: 0.5rem;
    padding-left: 0rem;
    transition: .3s ease-in-out;

    &:hover {
      background-color: #1B496599;
    }

    &-name {
      font-size: 2rem;
      margin-bottom: 0.5rem;
      margin-top: 0;
    }

    &-social {
      display: flex;
      gap: 0.5rem;

      &> svg {
        height: 1.5rem;
        width: 1.5rem;
        cursor: pointer;
        transform: scale(1);
        transition: 0.3s ease-in-out;
  
        &:hover {
          transform: scale(1.25);
        }
      }
    }

    &-basic {
      font-size: 1rem;
      padding-left: 9rem;
      margin: 2.5rem 0;
      text-align: justify;
    }
  }

  &__download-btn {
    text-decoration: none;
    color: $textColor;
    display: flex;
    gap: 1rem;
    padding: 1rem 2rem;
    border: 0.25rem solid $supportColor;
    border-radius: 0.5rem;
    font-size: 1rem;
    font-weight: bold;
    align-items: center;
    transform: scale(1);
    transition: 0.3s ease-in-out;

    &:hover {
      transform: scale(1.025);
    }
  }
}