.career {
  &-container {
    min-height: 38rem;
    background-color: $secondaryBgColor;
    padding: 5rem;
    padding-top: 2rem;
  }

  &__wrapper {
    border-left: 0.25rem solid $supportColor;
    // border-radius: 0.5rem;
    // padding: 2rem 0;

    &-group {
      transition: .3s ease-in-out;
      margin-bottom: 2rem;
      padding-bottom: 2rem;
      padding-right: 2rem;
    
      &:hover {
        background-color: #1B496522;
      }
    }

    &-title {
      display: flex;
      height: 2rem;
      align-items: center;
      cursor: pointer;
      margin-bottom: 1rem;

      &-icon {
        display: inline-flex;
        height: 2rem;
        width: 2rem;
        align-items: center;
        justify-content: center;
        // margin-right: 1rem;
        background-color: $textColor;
        font-size: 3rem;
        border-radius: 50%;
        color: $BgColor;
        font-weight: bold;
        // line-height: 3rem;
        transform: translateX(-1.15rem);
        padding-bottom: 0.8rem;
      }

      &-text {
        display: inline-flex;
        margin: 0;
      }
    }

    &-bubble {
      display: flex;
      margin-top: 2.5rem;
      align-items: center;

      &-icon {
        display: inline-flex;
        // height: 0.25rem;
        // width: 2rem;
        align-items: center;
        justify-content: center;
        background-color: $textColor;
        border-radius: 0.2rem;
        color: $supportColor;
        transform: translateX(-0.2rem);
        color: $secondaryBgColor;
        padding: 0.5rem 1rem;
        font-weight: bold;
      }

      &-box {
        display: flex;
        flex-direction: column;
        justify-content: center;
        border-radius: 50%;
        border: 0.25rem solid $supportColor;
        padding: 2.5rem;
        max-width: 50%;
        min-height: 15rem;
        margin-left: 2rem;
        transition: .3s ease-in-out;
    
        &:hover {
          background-color: #1B496599;
        }

        .bubble-box {
          &__header {
            width: 70%;
            margin: 0 auto;
            display: inline-flex;
            flex-direction: column;
            text-align: center;

            &-title {
              margin: 0;
              display: inline-flex;
              justify-content: center;
            }
            &-subtitle {
              font-style: italic;
            }
          }

          &__details {
            text-align: justify;
            margin: 1rem 0;
          }

          &__footer {
            width: 70%;
            margin: 0 auto;
            text-align: center;
            &-title {
              font-weight: bold;
            }
            &-subtitle {
              font-style: italic;
            }
          }
        }
      }

      &-arrow {
        width: 35%;
        height: 35%;
        min-height: 8rem;
        border-top: 0.25rem dashed $textColor;
        border-right: 0.25rem dashed $textColor;
        border-top-left-radius: 0;
        border-top-right-radius: 75%;
        position: relative;
        top: 4rem;

        svg {
          position: absolute;
          right: -1.125rem;
          bottom: 0;
          transform: rotate(140deg);
          font-size: 2rem;
        }
      }

      &.right-bubble {
        .career__wrapper-bubble {
          &-box {
            margin-left: 0;
            margin-right: 2rem;
          }

          &-arrow {
            border-right: none;
            border-left: 0.25rem dashed $textColor;
            border-top-left-radius: 75%;
            border-top-right-radius: 0;
            margin-left: auto;

            svg {
              left: -1.125rem;
              transform: rotate(140deg);
            }
          }
        }
      }
    }
  }
}